var locales = locales || {}; locales['pt_BR'] = {"domain":"app","locale_data":{"app":{"":{"Project-Id-Version":"gitlab 1.0.0","Report-Msgid-Bugs-To":"","POT-Creation-Date":"2017-05-04 19:24-0500","MIME-Version":"1.0","Content-Type":"text/plain; charset=UTF-8","Content-Transfer-Encoding":"8bit","PO-Revision-Date":"2017-06-05 03:29-0400","Last-Translator":"Alexandre Alencar <alexandre.alencar@gmail.com>","Language-Team":"Portuguese (Brazil)","Language":"pt-BR","X-Generator":"Zanata 3.9.6","Plural-Forms":"nplurals=2; plural=(n != 1)","lang":"pt_BR","domain":"app","plural_forms":"nplurals=2; plural=(n != 1)"},"ByAuthor|by":["por"],"Commit":["Commit","Commits"],"Cycle Analytics gives an overview of how much time it takes to go from idea to production in your project.":["A Anlise de Ciclo fornece uma viso geral de quanto tempo uma ideia demora para ir para produo em seu projeto."],"CycleAnalyticsStage|Code":["Cdigo"],"CycleAnalyticsStage|Issue":["Tarefa"],"CycleAnalyticsStage|Plan":["Plano"],"CycleAnalyticsStage|Production":["Produo"],"CycleAnalyticsStage|Review":["Reviso"],"CycleAnalyticsStage|Staging":["Homologao"],"CycleAnalyticsStage|Test":["Teste"],"Deploy":["Implantao","Implantaes"],"FirstPushedBy|First":["Primeiro"],"FirstPushedBy|pushed by":["publicado por"],"From issue creation until deploy to production":["Da criao de tarefas at a implantao para a produo"],"From merge request merge until deploy to production":["Da incorporao do merge request  at a implantao em produo"],"Introducing Cycle Analytics":["Apresentando a Anlise de Ciclo"],"Last %d day":["ltimo %d dia","ltimos %d dias"],"Limited to showing %d event at most":["Limitado a mostrar %d evento no mximo","Limitado a mostrar %d eventos no mximo"],"Median":["Mediana"],"New Issue":["Nova Tarefa","Novas Tarefas"],"Not available":["No disponvel"],"Not enough data":["Dados insuficientes"],"OpenedNDaysAgo|Opened":["Aberto"],"Pipeline Health":["Sade da Pipeline"],"ProjectLifecycle|Stage":["Etapa"],"Read more":["Ler mais"],"Related Commits":["Commits Relacionados"],"Related Deployed Jobs":["Jobs Relacionados Incorporados"],"Related Issues":["Tarefas Relacionadas"],"Related Jobs":["Jobs Relacionados"],"Related Merge Requests":["Merge Requests Relacionados"],"Related Merged Requests":["Merge Requests Relacionados"],"Showing %d event":["Mostrando %d evento","Mostrando %d eventos"],"The coding stage shows the time from the first commit to creating the merge request. The data will automatically be added here once you create your first merge request.":["O estgio de codificao mostra o tempo desde o primeiro commit at a criao do merge request. \\nOs dados sero automaticamente adicionados aqui uma vez que voc tenha criado seu primeiro merge request."],"The collection of events added to the data gathered for that stage.":["A coleo de eventos adicionados aos dados coletados para esse estgio."],"The issue stage shows the time it takes from creating an issue to assigning the issue to a milestone, or add the issue to a list on your Issue Board. Begin creating issues to see data for this stage.":["O estgio em questo mostra o tempo que leva desde a criao de uma tarefa at a sua assinatura para um milestone, ou a sua adio para a lista no seu Painel de Tarefas. Comece a criar tarefas para ver dados para esta etapa."],"The phase of the development lifecycle.":["A fase do ciclo de vida do desenvolvimento."],"The planning stage shows the time from the previous step to pushing your first commit. This time will be added automatically once you push your first commit.":["A fase de planejamento mostra o tempo do passo anterior at empurrar o seu primeiro commit. Este tempo ser adicionado automaticamente assim que voc realizar seu primeiro commit."],"The production stage shows the total time it takes between creating an issue and deploying the code to production. The data will be automatically added once you have completed the full idea to production cycle.":["O estgio de produo mostra o tempo total que leva entre criar uma tarefa e implantar o cdigo na produo. Os dados sero adicionados automaticamente at que voc complete todo o ciclo de produo."],"The review stage shows the time from creating the merge request to merging it. The data will automatically be added after you merge your first merge request.":["A etapa de reviso mostra o tempo de criao de um merge request at que o merge seja feito. Os dados sero automaticamente adicionados depois que voc fizer seu primeiro merge request."],"The staging stage shows the time between merging the MR and deploying code to the production environment. The data will be automatically added once you deploy to production for the first time.":["O estgio de estgio mostra o tempo entre a fuso do MR e o cdigo de implantao para o ambiente de produo. Os dados sero automaticamente adicionados depois de implantar na produo pela primeira vez."],"The testing stage shows the time GitLab CI takes to run every pipeline for the related merge request. The data will automatically be added after your first pipeline finishes running.":["A fase de teste mostra o tempo que o GitLab CI leva para executar cada pipeline para o merge request relacionado. Os dados sero automaticamente adicionados aps a concluso do primeiro pipeline."],"The time taken by each data entry gathered by that stage.":["O tempo necessrio para cada entrada de dados reunida por essa etapa."],"The value lying at the midpoint of a series of observed values. E.g., between 3, 5, 9, the median is 5. Between 3, 5, 7, 8, the median is (5+7)/2 = 6.":["O valor situado no ponto mdio de uma srie de valores observados. Ex., entre 3, 5, 9, a mediana  5. Entre 3, 5, 7, 8, a mediana  (5 + 7) / 2 = 6."],"Time before an issue gets scheduled":["Tempo at que uma tarefa seja planejada"],"Time before an issue starts implementation":["Tempo at que uma tarefa comece a ser implementada"],"Time between merge request creation and merge/close":["Tempo entre a criao do merge request e o merge/fechamento"],"Time until first merge request":["Tempo at o primeiro merge request"],"Time|hr":["h","hs"],"Time|min":["min","mins"],"Time|s":["s"],"Total Time":["Tempo Total"],"Total test time for all commits/merges":["Tempo de teste total para todos os commits/merges"],"Want to see the data? Please ask an administrator for access.":["Precisa visualizar os dados? Solicite acesso ao administrador."],"We don't have enough data to show this stage.":["No temos dados suficientes para mostrar esta fase."],"You need permission.":["Voc precisa de permisso."],"day":["dia","dias"]}}};

